import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Artykul } from 'models/artykul';
import { ArtykulyService } from './artykuly.service';
import { KoszykService } from './koszyk.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'shop';
  licznik: number = 0;
  artykuly: Artykul[] = [];

  constructor(private appTitle: Title, private artykulyService: ArtykulyService, private koszykService: KoszykService){}

  ngOnInit(){
    this.appTitle.setTitle("Shop");
    this.artykulyService.pobierzArtykuly().subscribe(res => this.artykuly = res);
  }

  onArtykulClick(val: boolean){
    if(val)this.licznik++;
    else this.licznik--;
  }
}
