import { Injectable } from '@angular/core';
import { Artykul } from 'models/artykul';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class ArtykulyService {

  private artykuly: BehaviorSubject<Artykul[]> = new BehaviorSubject<Artykul[]>([]);

  constructor() {
    let temp = this.artykuly.value;

    setTimeout(() => {
      for(let i = 0; i < 10; i++){
        temp.push(
          {id: i,
          nazwa: 'Produkt ' + i,
          opis: 'Lorem ipsum',
          cena: 1 * i}
        );
      }
    }, 750);
  }

  pobierzArtykuly(): Observable<Artykul[]>{
    return this.artykuly.asObservable();
  }
}
