import { Injectable } from '@angular/core';
import { Artykul } from 'models/Artykul';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { ArtykulyService } from './artykuly.service';

@Injectable({
  providedIn: 'root'
})
export class KoszykService {

  wybraneArtykuly: BehaviorSubject<Artykul[]> = new BehaviorSubject<Artykul[]>([]);

  constructor() { }

  pobierzKoszyk(): Observable<Artykul[]>{
    return this.wybraneArtykuly.asObservable();
  }

  wyczyscKoszyk(): Observable<void>{
    this.wybraneArtykuly.next([]);
    return of();
  }

  dodajDoKoszyka(artykul: Artykul): Observable<void>{
      let temp = this.wybraneArtykuly.value;
      temp.push(artykul);
      this.wybraneArtykuly.next(temp);
      return of();
  }
}
